<template>
    <div class="home">
        <!--顶部开始-->
        <div class="index_top">
            <div class="index_top_left">
                <img src="../../assets/images/logo.png" alt="">
            </div>
            <div class="index_top_center">
                <!--<div>{{ lang_list.home_mean_1 }}</div>-->
                <!--<div @click="toGong()">{{ lang_list.home_mean_2 }}</div>-->
                <!--<div @click="toPdf">白皮书</div>-->
                <!--<div style="color: #f2f2f2;">NFT</div>-->
            </div>
            <div class="index_top_right" style="width: 75%;">
                <img src="../../assets/images/mean.png" alt="" style="height: 30px;margin-top: 15px;display: inline-block;"
                     @click="showMean">
                <!--EN/中-->
            </div>
            <div class="index_clear"></div>
        </div>
        <!--顶部结束-->
        <!--头部开始-->
        <div class="index-header" style="text-align: center;">
            <div class="col-xs-12 col-sm-8 gong-content" style="float: none;display: inline-block;">
                <div class="gong-title">{{ lang_list.home_mean_2 }}</div>
                <div class="gong-tab">
                    <div>{{ lang_list.gong_mas1 }}</div>
                    <div>{{ masBanner }} MAS</div>
                </div>
                <div class="gong-display-tab" style="text-align: left;">
                    <div class="gong-display-45">from</div>
                    <div class="gong-display-right">to</div>
                </div>
                <div class="gong-display-item" style="text-align: left;">
                    <div class="gong-display-item-left">
                        <img src="../../assets/images/usdt.png" alt="" />
                        <span>USDT</span>
                    </div>
                    <div class="gong-display-item-middle">
                        <img src="../../assets/images/a.png" alt="" />
                    </div>
                    <div class="gong-display-item-right">
                        <img src="../../assets/images/msn.png" alt="" />
                        <span>MAS</span>
                    </div>
                </div>
                <div style="display: flex;justify-content: space-between;">
                    <div style="padding: 8px 0 ;">{{ lang_list.home_mean_2 }} MAS：</div>
                    <input type="number" v-model="masNum" :placeholder="lang_list.gong_mas2" class="gong-display-input"
                    @keyup="changeUsdt()" />
                </div>
                <div class="gong-display-submit">
                    <div class="index-header-button" @click="toBuy()">{{ lang_list.home_mean_2 }}</div>
                </div>
                <div class="gong-display-price">
                    <div>{{ lang_list.gong_mas3 }}：</div>
                    <div>1MAS={{ price }}USDT</div>
                </div>
                <div style="display: flex;justify-content: space-between;">
                    <div>{{ lang_list.gong_mas4 }}：</div>
                    <div>{{ useUsdt }}USDT</div>
                </div>
            </div>
        </div>
        <!--头部结束-->
        <!--菜单弹窗开始-->
        <div class="loadWindow" v-if="meanShow" @click="closeMean()"></div>
        <div  v-if="meanShow" class="index_pop_content">
            <div class="index_pop_top">
                <div class="index_pop_top_left">
                    <span>MAS</span>
                </div>
                <div class="index_pop_top_right">

                </div>
                <div class="index_clear"></div>
            </div>
            <div class="index_pop_middle" @click="toIndex()">
                <div class="index_pop_middle_left">
                    <span>{{ lang_list.home_mean_1 }}</span>
                </div>
                <div class="index_pop_middle_right">
                    <img src="../../assets/images/left.png" alt="">
                </div>
                <div class="index_clear"></div>
            </div>
            <div class="index_pop_middle"  @click="toGong()">
                <div class="index_pop_middle_left">
                    <span>{{ lang_list.home_mean_2 }}</span>
                </div>
                <div class="index_pop_middle_right">
                    <img src="../../assets/images/left.png" alt="">
                </div>
                <div class="index_clear"></div>
            </div>
            <div class="index_pop_middle"  @click="toPdf()">
                <div class="index_pop_middle_left">
                    <span>{{ lang_list.home_mean_3 }}</span>
                </div>
                <div class="index_pop_middle_right">
                    <img src="../../assets/images/left.png" alt="">
                </div>
                <div class="index_clear"></div>
            </div>
            <div class="index_pop_middle">
                <div class="index_pop_middle_left">
                    <span style="color: #dddddd;">NFT</span>
                </div>
                <div class="index_pop_middle_right">
                </div>
                <div class="index_clear"></div>
            </div>
            <div class="index_pop_middle" >
                <div class="index_pop_middle_left">
                    <span style="color: #dddddd;">{{ lang_list.home_mean_4 }}</span>
                </div>
                <div class="index_pop_middle_right">
                </div>
                <div class="index_clear"></div>
            </div>
            <div class="index_pop_middle">
                <div class="index_pop_middle_left">
                    <span style="color: #dddddd;">{{ lang_list.home_mean_5 }}</span>
                </div>
                <div class="index_pop_middle_right">
                </div>
                <div class="index_clear"></div>
            </div>
            <div class="index_pop_footer">
                <div class="index_pop_button" :class="{'index_pop_button_active':lang=='zh'}"  @click="switchLang('zh')">
                    简体中文
                </div>
                <div class="index_pop_button" :class="{'index_pop_button_active':lang=='en'}"  @click="switchLang('en')">
                    English
                </div>
            </div>
        </div>
        <!--菜单弹窗结束-->
    </div>
</template>
<style>

</style>
<script>
    let provider, signer, defaultAccount, networkVersion;
    let usdtContract, usdtAddress = '0x55d398326f99059fF775485246999027B3197955';
    let conContract, conAddress = '0xB326242fa8E9Cf2931ba38494747f9de1dc27960';
    let masContract, masAddress = '0x089AEFab91c5ce2D0534B31C711bA1633C7bf2f6';
    export default {
        data() {
            return {
                lang: 'en',
                lang_list: {},
                address: '',
                short_address: '连接钱包',
                disable: false,
                show: false,
                meanShow: false,
                type: '',
                num: 1000000,
                masBanner:0,
                usdtBanner:0,
                price:0,
                useUsdt:0,
                masNum:'',
            }
        },
        created: function () {
            this.checkNet()
        },
        async mounted() {
            this.lang = sessionStorage.getItem('lang') ? sessionStorage.getItem('lang') : 'en';
            this.lang_list = this.en;
            if (this.lang == 'zh') {
                this.short_address = 'connect';
                this.lang_list = this.zh;
            }
            window.addEventListener('load', async () => {
                // Modern dapp browsers...
                if (window.ethereum) {
                    try {
                        window.web3 = new Web3(ethereum);
                        // Request account access if needed
                        await ethereum.enable();
                        // Acccounts now exposed
                        web3.eth.sendTransaction({/* ... */});
                    } catch (error) {
                        // User denied account access...
                    }
                }
                // Legacy dapp browsers...
                else if (window.web3) {
                    window.web3 = new Web3(web3.currentProvider);
                    // Acccounts always exposed
                    web3.eth.sendTransaction({/* ... */});
                }
                // Non-dapp browsers...
                else {
                    console.log('Non-Ethereum browser detected. You should consider trying MetaMask!');
                }
            });
        },
        methods: {
            //显示菜单
            showMean(){
                this.meanShow = true;
            },
            //关闭菜单
            closeMean(){
                this.meanShow = false;
            },
            async toBuy(){
                let _this = this;
                const gasPrice = await provider.getGasPrice()
              if(this.masNum<10000){
                  this.$toast(_this.lang_list.gong_warn1);
                  return false;
              }
                if(this.disable){
                    return false;
                }
                this.disable = true;
                let etherNum = ethers.utils.parseEther (this.useUsdt.toString());
                await usdtContract.allowance(defaultAccount,conAddress).then(async res =>{
                    if(parseFloat(ethers.utils.formatEther(res))>0){
                        let tx1 = await usdtContract.approve(conAddress,ethers.utils.parseEther('0'),{
                            gasPrice: gasPrice,
                            gasLimit:400000
                        })
                    }
                    try{
                        let tx = await usdtContract.approve(conAddress,etherNum,{
                            gasPrice: gasPrice,
                            gasLimit:400000
                        })
                        if(tx){
                            let res = await conContract.buyMass(etherNum,{
                                gasPrice: gasPrice,
                                gasLimit:1200000
                            })
                            if(res){
                                _this.disable = false;
                                _this.$toast(_this.lang_list.gong_warn2);
                                setTimeout(function () {
                                    location.reload();
                                },3000)
                            }
                        }
                    }catch (e) {
                        _this.disable = false;
                        _this.$toast(_this.lang_list.gong_warn3);
                        setTimeout(function () {
                            location.reload();
                        },3000)
                    }
                }).catch(err => {
                    _this.disable = false;
                    _this.$toast(_this.lang_list.gong_warn3);
                    setTimeout(function () {
                        location.reload();
                    },3000)
                    return false;
                });
            },
            changeUsdt(){
                if(this.masNum>=0){
                    this.useUsdt = parseFloat(this.masNum)*parseFloat(this.price);
                }else{
                    this.useUsdt=0;
                }
            },
            connectWallet() {
                if (this.address) {
                    this.short_address = this.address.substr(0, 3) + '...' + this.address.substr(this.address.length - 3)
                } else {
                    this.checkNet()
                }
            },
            switchLang() {
                if (this.lang == 'zh') {
                    this.lang == 'en'
                    sessionStorage.setItem('lang', 'en');
                } else {
                    this.lang == 'zh'
                    sessionStorage.setItem('lang', 'zh');
                }
                location.reload();
            },
            getUrlParam(name) {
                var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)"); //构造一个含有目标参数的正则表达式对象
                var r = window.location.search.substr(1).match(reg);  //匹配目标参数
                if (r != null) return unescape(r[2]);
                return null; //返回参数值
            },
            async checkNet() {
                let _this = this;
                try {
                    provider = new ethers.providers.Web3Provider(web3.currentProvider);
                    signer = provider.getSigner();
                    _this.checkLogin()
                } catch (e) {
                    _this.$toast(_this.lang_list.home_warn1);
                }

            },
            async checkLogin() {
                let _this = this;
                setInterval(function () {
                    window.ethereum.enable()
                        .then(function (accounts) {
                            if (accounts.length == 0 && defaultAccount != "") {
                                window.location.reload();
                                return;
                            }
                            if (accounts.length == 0) {
                                return;
                            }
                            if (accounts[0] != defaultAccount) {
                                defaultAccount = accounts[0];
                                window.location.reload();
                                console.log("account change, start bat updateData!")
                            }
                        })
                        .catch(function (reason) {
                            // Handle error. Likely the user rejected the login:
                            console.log(reason === "User rejected provider access")
                        })
                }, 1000);
                window.ethereum.enable()
                    .then(function (accounts) {
                        if (accounts.length == 0) {
                            console.log('33333');
                            return;
                        }
                        defaultAccount = accounts[0];
                        console.log("defaultAccount: ", defaultAccount);
                        networkVersion = window.ethereum.networkVersion
                        if (networkVersion == 1) {  //以太坊主网
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 42) {   //以太坊测试网  K
                            _this.type = 'ETH';
                            _this.$toast(_this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 128) {  //HECO主网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 256) { //HECO测试网
                            _this.type = 'HT';
                            _this.$toast(_this.lang_list.home_warn1);
                            return false;
                        } else if (networkVersion == 56) { //币安主网
                            _this.type = 'BNB';
                        } else {
                            _this.$toast(_this.lang_list.home_warn1);
                            return false;
                        }
                        usdtContract = new ethers.Contract(usdtAddress, usdtAbi, signer);
                        masContract = new ethers.Contract(masAddress, masAbi, signer);
                        conContract = new ethers.Contract(conAddress, conAbi, signer);
                        if (defaultAccount != '') {
                            _this.address = defaultAccount;
                            _this.getMasBanner()
                            _this.getUsdtBanner()
                            _this.getPrice()
                        }
                    });
                setTimeout(function () {
                    if (defaultAccount) {
                        _this.address = defaultAccount;
                        _this.short_address = _this.address.substr(0, 3) + '...' + _this.address.substr(_this.address.length - 3)
                    }

                }, 500)
            },
            //获取单价
            async getPrice() {  //获取余额
                let _this = this;
                await conContract.getMASToUsdtRate().then(res => {
                    console.log(res)
                    let etherString = ethers.utils.formatEther(res);
                    _this.price =parseFloat(ethers.utils.formatEther(res)*1000000);
                    console.log(_this.price)
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            async getUsdtBanner() {  //获取余额
                let _this = this;
                await usdtContract.balanceOf(defaultAccount).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.usdtBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                    console.log(_this.usdtBanner)
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            async getMasBanner() {  //获取余额
                let _this = this;
                await masContract.balanceOf(conAddress).then(res => {
                    // let etherString = ethers.utils.formatEther(res);
                    _this.masBanner = Math.floor(parseFloat(ethers.utils.formatEther(res)) * 100) / 100;
                    console.log(_this.masBanner)
                }).catch(err => {
                    console.log(err);
                    return;
                });
            },
            toPdf(){
                if (this.lang == 'zh') {
                    window.open('https://masnft.github.io/zh.pdf')
                }
                else{
                    window.open('https://masnft.github.io/en.pdf')
                }

            },
            toGong() {
                this.meanShow = false;
            },
            toIndex() {
                this.$router.push({
                    path: '/home',
                    query: {}
                })
            },
        }
    }
</script>


